.footer {
    display: flex;
    justify-self: center;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    margin: 0 auto;
    padding: 30px 0;
    border-top: 2px solid #eee;
    width: 80%;

    p {
        opacity: 0,5;
    }
}

@media (max-width: 600px) {
    .footer {

        p {
            font-size: 12px;
            text-align: center;
        }
    }
}